@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar_background: #09409b;
  --white: #ffffff;
  --body_background: #106fa9 ;
}
* {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  color: white;
}
body {
  width: 100wh;
  height: 100vh;
  overflow: hidden;
}

.sidebar-body-container {
  display: flex;
}

.header {
  background-color: antiquewhite;
  width: 100wh;
  font-size: 3rem;
  text-align: left;
  display: flex;
}
.main-container {
  display: flex;
  flex-direction: column;
}
button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.quick-sort-pivot-body {
  background-color: #6a93a7;
  padding: 10px;
  border-radius: 3px;
}

.quick-sort-pivot-heading {
  margin-bottom: 10px;
}

.quick-sort-pivot-checkbox-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.3fr 0.3fr;
  /* gap: 0px 0px; */
  /* grid-auto-flow: row; */
  text-align: left;
}

.quick-sort-pivot-checkbox {
  text-align: left;
}

