.body-main-container {
  display: flex;
  flex-wrap: wrap;
  background-color: var(--body_background);
  flex-basis: 70%;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}
.body-controller-container {
  position: absolute;
  top: 0;
  left: 0;
}

.about-algo-button {
  position: absolute;
  top: 0;
  right: 0;
}

.bars-container {
  margin-top: 10vh;
}
.container {
  position: relative;
  width: 600px;
  margin: auto;
  bottom: 5vh;
  display: flex;
}
.bar {
  height: 100px;
  background-color: #bab6ab;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 3px 3px 0 0;
  position: absolute;
  border: 1px solid black;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: black;
}
.barcount {
  color: black;
}
.bar:hover .barcount {
  font-size: 2rem !important;
  color: white;
  -webkit-transform: translateY(2rem);
          transform: translateY(2rem);
}

.blue {
  background-color: rgb(0, 174, 255);
}

.green {
  background-color: rgb(151, 209, 151);
}

.red {
  background-color: #ff0000;
}

.yellow {
  background-color: rgb(255, 165, 0);
}

.purple {
  background-color: purple;
}

.generate-new-array-btn-container {
  margin: 50px auto;
  text-align: center;
}

.array-input-container {
  position: relative;
  text-align: center;
}

.array-input {
  background-color: white;
  color: black;
  font-size: 20px;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  outline: none;
  padding: 7px 12px;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  max-width: 90%;
}
.input-array-btn {
  margin: 5px 10px !important;
}

.shuffle-array-btn {
  margin: 5px auto !important;
}
.array-input-label {
  margin-right: auto;
  position: absolute;
}

.sidebar-container {
  flex-basis: 30%;
}

.controller-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-basis: 30%;
  background-color: var(--sidebar_background);
  min-height: 90vh;
  border-top: 1px solid var(--body_background);
  padding-top: 30px;
}

.sliders-container {
  margin: 10% auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.github-logo-container {
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  align-items: center;
}

.sidebar-visitor-badge {
  width: 80px;
  margin: 0 auto 5px;
}

.header-container {
  min-height: 50px;
  background-color: #232651;
}
.header-heading-container {
  text-align: left;
}
.header-heading {
  font-family: "Stylish", sans-serif;
  font-size: 3rem;
  margin-left: 5px;
}
h1{
  text-align: center;
}
